        p8:'Gwn specjalizacj dr Miosia jest reinforcement learning, czyli uczenie ze wzmocnieniem. Jest to w pewnym sensie metoda prb i bdw. Za pomoc zestawu kar i nagrd, staramy si nauczy system, co ma robi. Dla przykadu, uczcy si robot dostanie nagrod o wartoci 1 (jeli dotrze do zadanej pozycji) lub 1, czyli kar (jeli np. uderzy w przeszkod). W pozostaych przypadkach nagroda ma warto 0. Dr Mio opowiedzia nam o konkursie organizowanym przez Uniwersytet Stanforda, w ktrym mia przyjemno wzi udzia. Polega on na tym, aby nauczy szkielet chodzi (od samych podstaw). Okazao si to by nie lada wyzwaniem, w ktrym gwn rol odgrywa wanie reinforcement learning.',
        p9:'Dr Burdukiewicz moe by niektrym z Was znany ze spotka STWURa (Stowarzyszenia Wrocawskich Uytkownikw R). Jest on bioinformatykiem, z szerokim umiechem, zawodowo zajmujcym si analiz funkcjonaln biaek i medycyn spersonalizowan.  Na pocztku wykadu, zaznajomilimy si z podstawowymi informacjami na temat aminokwasw, biaek i ich rzdowoci, a take amyloidw - nieprawidowych biaek, zwizanych z chorobami takimi jak grulica, reumatoidalne zapalenie staww czy choroba Parkinsona. W duym skrcie: do przewidywania czy biako jest amyloidem, czy te nie, prowadzcy wytrenowa predykatory, stosujc drzewa losowe i metod n-gram. Najlepszy okaza si by AmyloGram (zainteresowanych odsyam do pakietw R-owych: biogram i Amylogram).',
        p10:'Na koniec dodam, e zachcam wszystkich do brania udziau w konferencjach. Oprcz dodatkowej wiedzy, ktr wyniesiecie po takim weekendzie, poznacie te mnstwo ludzi o podobnych zainteresowaniach, porozmawiacie ze studentami z innych uczelni, by moe kto was namwi do udziau w ciekawym projekcie lub kolejnej konferencji. No i przede wszystkim - po udanym wyjedzie, spodziewajcie si solidnego przypywu motywacji!',
