import imgArt1 from '../img/articlesImg/IG_LS1.jpeg'
import ArtHeader from '../ArtH';
import ArtImg from '../ArtI';
import ArtParagraph from '../ArtP';
import ArtLi from '../ArtList';
import React, { Component } from 'react';
class Art2 extends Component {
    state = { 
        title: 'LICZ SOWA I',
        title2: 'Na pocztku byo sowo, a potem dystans',
        author: 'Iwo Gruca',
        date: '03 - 2019',
        img: imgArt1,
        p1:'Matematyka posiada wiele zastosowa. Pierwsze, co przychodzi na myl u wikszoci z nas, to finanse, analiza danych (po staropolsku data science), bd teoretyczna oraz praktyczna fizyka. Jednak okazuje si, e  krlowa nauk pojawia si rwnie w mniej spodziewanych miejscach. Ten krtki artykulik jest w zamierzeniu jednym z serii popularnonaukowych materiaw, w ktrych postaram si opisa powizania matematyki z humanistyk. Nasz pierwsz nauk humanistyczn bdzie lingwistyka. Czy da si zmierzy wiersz? Czy moemy przewidzie odpowied rozmwcy na zadane przez nas pytanie (oczywicie zakadajc, e nie jest ono retoryczne)? Czy moemy stworzy szyfr nie do zamania? Te oraz podobne problemy stara si rozwiza lingwistyka matematyczna. ',
        p2:'W tym artykule, chciabym Wam opisa, czym zajmuj si dodatkowo w ramach swoich studiw. Niedawno, podczas pracy nad pewnym projektem, miaem styczno z problematyk podobiestwa sw. Pierwszym problemem (lub za lwowsk szko matematyczn: problematem) jest kwestia jak traktowa sowo?. Wbrew pozorom, stanowi to bardzo szeroki temat. O sowach mona myle jako o skoczonych cigach znakw (tzw. diakrytw), ale nic nie stoi na przeszkodzie, eby potraktowa je jako wykresy wysokoci dwiku od czasu. Dla wygody przyjmiemy jednak t pierwsz definicj (tak graficzn posta sowa wystpujc w sowniku nazywa si w lingwistyce leksemem). Kolejn przeszkod stojc na naszej drodze jest stwierdzenie co to znaczy, e sowa s blisko lub daleko od siebie. Dziedzin matematyki, w ktrej krgu zainteresowa znajduje si pojcie odlegoci jest topologia. W jzyku teje teorii odlego to nic innego jak warto metryki  funkcji o specjalnych wasnociach: ', 
        p3: 'LICZ SOWA metryki na sobie, eby mc si zdystansowa do kolejnego wpisu. W midzyczasie wszystkim zainteresowanych czym s leksemy, albo jak z pczka zrobi kubek, polecam lektur Wstpu do jzykoznawstwa Renaty Grzegorczykowej oraz do Topologii oglnej Ryszarda Engelkinga. Niech sowa bd z Wami! ',
        items:[
            {id:1, txt: 'd(x,y) = 0 wtedy i tylko wtedy, gdy  x = y (identyczno nierozrnialnych elementw), '},
            {id:2, txt: 'd(x,y) = d(y,x)(symetria), '},
            {id:3, txt: 'd(x,z)  d(x,y) + d(y,z)(nierwno trjkta). '},
        ]
    
    }
    render() { 
        const {title,title2,img, author,date, p1,p2,p3,items} = this.state
        return (
            <div className="articlePage">
                <ArtHeader title = {title} title2 = {title2} author = {author} img = {img} date = {date}/>
                <ArtImg img = {img}/>
                <ArtParagraph p = {p1}/>
                <ArtParagraph p = {p2}/>
                <ul className="artPage__list">
                {items.map(item => <ArtLi key = {item.id} li = {item.txt}/>)}
                </ul>
                <ArtParagraph p = {p3}/>
              
            </div>
          );
    }
}
 
export default Art2;